import React from "react";
const Navbar = () => {
    return (
        <nav className="navbar navbar-expand-lg bg-light background">
            <div className="container-fluid">
                <a className="navbar-brand nav-link-c" href="\">Navbar</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <a className="nav-link nav-link-c active" aria-current="page" href="\">Home</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link nav-link-c active" href="\">About</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link active nav-link-c" href="\">Services</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link active nav-link-c" href="\">Contact</a>
                        </li>
                    </ul>
                    <form className="d-flex" role="search">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-success nav-link-c" type="submit">Search</button>
                    </form>
                </div>
            </div>
        </nav>
    )
}
export default Navbar;